.App {
  text-align: center;
  border: 4px solid black;
  width:700px;
   border-radius:10px ;
  margin:100px auto;
  height: auto;
  padding:5px 50px 150px ;
  background: linear-gradient(to bottom, #ffc933 0%, #ff99cc 100%) ;
  box-shadow: 0 1px 2px rgba(0,0,0,0.8);
  transition: all 0.3s ease-in-out;
 
}

.fulfilledWishes{
border: 2px dotted grey;

font-weight: bold;

}
.add , .up{
  height: 40px !important;
  background-color:lightseagreen;
  border: 2px solid white !important;
  
}

#inputWish{
  border-radius: 15px;
  height: 40px;
  width: 280px;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.btn{
  cursor: pointer;
 
  position: absolute;
  right:5px;
  top:30%;
  background-color: red;
}
.cmnbtn:hover{

 font-size:15px;
}
#wish{
  display:inline-block;
  margin-right: 50px;
  text-decoration: line-through;
}
 #wish:hover{

  font-size:20px;
 
 }
 
.time{
  font-size: 12px;
  color:saddlebrown;
}

.cmnbtn{
  cursor: pointer;
  height:25px;
  border-radius: 15px;
  margin:0px 5px;
  border: none;
  color: white;
  padding: 5px ;
  text-align: center;
  text-decoration: none;
  
  font-weight: bold;
  font-family:  'Trebuchet MS', sans-serif;

}

.upbtn{
  background-color:rgb(75, 124, 75);
  cursor: pointer;

  position: absolute;
  left:5px;
  top:30%;
}

.header{

  margin-bottom: 50px;
}


.Wishlist{
  position: relative;
  height:100%;
  width:100%;
  border:5px solid black;
  margin: 20px auto;
  border-radius: 15px;
  background-color:lightcoral;
  color:black;
  font-weight: bolder;
  padding-top:5px;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
